import React, { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import '../styles/QuizPage.css';

import useLocalStorage from '../hooks/useLocalStorage';

import { Button, Radio } from 'antd';
import Tree from 'react-d3-tree';

function createTreeDataForType1(problemString) {
    // Extract node values from the problem string
    const nodeValues = problemString.match(/"([^"]+)"/g).map(str => str.replace(/"/g, ''));

    // Assuming the order is root, root's left child, root's right child, etc.
    const treeData = {
        name: nodeValues[0],
        children: [
            {
                name: nodeValues[1],
                children: [
                    { name: nodeValues[3] },
                    { name: nodeValues[4] }
                ]
            },
            {
                name: nodeValues[2],
                children: [
                    { name: nodeValues[5] },
                    { name: nodeValues[6] }
                ]
            }
        ]
    };
    return treeData;
}
const MyTreeComponentType1 = ({ problem }) => {
    const [treeData, setTreeData] = useState(null);
  
    useEffect(() => {
      const data = createTreeDataForType1(problem);
      setTreeData([data]); // react-d3-tree    .
    }, [problem]);
    const scaleExtent = { min: 0.1, max: 1 };
    const translate = { x: 50, y: 200 };
    const separation = { siblings: 0.5, nonSiblings: 0.5 };
    return (
      <div id="treeWrapper" style={{ width: '50em', height: '20em' }}>
        {treeData && (
          <Tree
            data={treeData}
            scaleExtent={scaleExtent}
            translate={translate}
            separation={separation}
          />
        )}
      </div>
    );
  };


function createTreeDataForType2(problemString) {
  const nodeValues = problemString.match(/([a-z])=(-?\d+)/g).reduce((acc, pair) => {
    const [key, value] = pair.split('=');
    acc[key] = value;
    return acc;
  }, {});

  const treeData = {
    name: nodeValues['a'], // root node
    children: [
      {
        name: nodeValues['b'],
        children: [
          {
            name: nodeValues['d'],
            children: [
              {
                name: nodeValues['h']
              },
              {
                name: nodeValues['i']
              }
            ]
          },
          {
            name: nodeValues['e']
          }
        ]
      },
      {
        name: nodeValues['c'],
        children: [
          {
            name: nodeValues['f']
          },
          {
            name: nodeValues['g']
          }
        ]
      }
    ]
  };
  return treeData;
}

const MyTreeComponentType2 = ({ problem }) => {
  const [treeData, setTreeData] = useState(null);

  useEffect(() => {
    const data = createTreeDataForType2(problem);
    setTreeData([data]); // react-d3-tree    .
  }, [problem]);
  const scaleExtent = { min: 0.1, max: 1 };
  const translate = { x: 50, y: 200 };
  const separation = { siblings: 0.5, nonSiblings: 0.5 };
  return (
    <div id="treeWrapper" style={{ width: '50em', height: '20em' }}>
      {treeData && (
        <Tree
          data={treeData}
          scaleExtent={scaleExtent}
          translate={translate}
          separation={separation}
        />
      )}
    </div>
  );
};


